/* the popup window */
.cc-window,
.cc-revoke {
  position: fixed;
  // bottom: 1em;
  overflow: hidden;
  box-sizing: border-box; /* exclude padding when dealing with width */
  font-family: "Inter", sans-serif;
  font-size: 14px; /* by setting the base font here, we can size the rest of the popup using CSS `em` */
  line-height: 1.5em;

  display: flex;
  flex-wrap: nowrap;

  /* the following are random unjustified styles - just because - should probably be removed */
  z-index: 9998;
}

.cc-revoke {
  z-index: 9999;
}

.cc-window.cc-static {
  position: static;
}

/* 2 basic types of window - floating / banner */
.cc-window.cc-floating {
  padding: 2em;
  max-width: 24em; /* 1em == 16px therefore 24em == 384px */
  flex-direction: column;
}
.cc-window.cc-banner {
  // padding: 2em 1.8em;
  width: 95%;
  flex-direction: row;
  margin: auto;
}

.cc-revoke {
  // padding: 0.5em;
}
.cc-revoke:hover {
  text-decoration: underline;
}

.cc-header {
  font-size: 18px;
  font-weight: bold;
}

/* clickable things */
.cc-btn,
.cc-link,
.cc-close,
.cc-revoke {
  cursor: pointer;
}

.cc-link {
  opacity: 0.8;
  display: inline-block;
  padding: 0.2em;
  text-decoration: underline;
}
.cc-link:hover {
  opacity: 1;
}
.cc-link:active,
.cc-link:visited {
  color: initial;
}

.cc-btn {
  display: block;
  // padding: 0.4em 0.8em;
  // font-size: 0.9em;
  // font-weight: bold;
  // border-width: 2px;
  // border-style: solid;
  text-align: center;
  white-space: nowrap;
}

